<template>
    <div class="bmd-layout-container bmd-drawer-f-l bmd-drawer-overlay">
      <header class="bmd-layout-header">
        <div class="navbar navbar-light bg-faded">
          <button class="navbar-toggler" type="button" data-toggle="drawer" data-target="#dw">
            <span class="sr-only">Toggle drawer</span>
            <i class="material-icons">menu</i>
          </button>
          <router-link class="router-link" to="/">
            Your Dish
          </router-link>
          <ul class="nav navbar-nav">
            <li class="nav-item">
              <span v-if="selectedRestName" class="text-primary">{{selectedRestName}}</span>
            </li>
          </ul>
        </div>
      </header>
      <div id="dw" class="bmd-layout-drawer bg-faded">
        <header>
          <a class="navbar-brand">식당</a>
        </header>
        <ul class="list-group">
          <router-link data-toggle="drawer" data-target="#dw" :to="`/menu/1/${today()}/${getMealType()}`" class="list-group-item">R3 하모니</router-link>
          <router-link data-toggle="drawer" data-target="#dw" :to="`/menu/2/${today()}/${getMealType()}`" class="list-group-item">R3 쿠디</router-link>
          <router-link data-toggle="drawer" data-target="#dw" :to="`/menu/3/${today()}/${getMealType()}`" class="list-group-item">R5 B1F</router-link>
          <router-link data-toggle="drawer" data-target="#dw" :to="`/menu/4/${today()}/${getMealType()}`" class="list-group-item">R5 B2F</router-link>
          <router-link data-toggle="drawer" data-target="#dw" :to="`/menu/5/${today()}/${getMealType()}`" class="list-group-item">R5 5F</router-link>
          <router-link data-toggle="drawer" data-target="#dw" :to="`/menu/6/${today()}/${getMealType()}`" class="list-group-item">R4 레인보우</router-link>
          <router-link data-toggle="drawer" data-target="#dw" :to="`/menu/7/${today()}/${getMealType()}`" class="list-group-item">R4 밀키웨이</router-link>
          <router-link data-toggle="drawer" data-target="#dw" :to="`/menu/8/${today()}/${getMealType()}`" class="list-group-item">R4 오아시스</router-link>
          <router-link data-toggle="drawer" data-target="#dw" :to="`/menu/9/${today()}/${getMealType()}`" class="list-group-item">스마트</router-link>
          <router-link data-toggle="drawer" data-target="#dw" :to="`/menu/10/${today()}/${getMealType()}`" class="list-group-item">투게더</router-link>
          <router-link data-toggle="drawer" data-target="#dw" :to="`/menu/11/${today()}/${getMealType()}`" class="list-group-item">패밀리</router-link>
          <router-link data-toggle="drawer" data-target="#dw" :to="`/menu/12/${today()}/${getMealType()}`" class="list-group-item">폴라리스</router-link>
          <router-link data-toggle="drawer" data-target="#dw" :to="`/menu/13/${today()}/${getMealType()}`" class="list-group-item">전자수원인재개발원</router-link>
          <router-link data-toggle="drawer" data-target="#dw" :to="`/menu/14/${today()}/${getMealType()}`" class="list-group-item">삼성미래기술캠퍼스</router-link>
          <router-link data-toggle="drawer" data-target="#dw" :to="`/menu/15/${today()}/${getMealType()}`" class="list-group-item">서울R&D 1단지</router-link>
          <router-link data-toggle="drawer" data-target="#dw" :to="`/menu/16/${today()}/${getMealType()}`" class="list-group-item">서울R&D 2단지</router-link>
        </ul>
      </div>

      <main class="bmd-layout-content">
        <router-view/>
      </main>
    </div>

</template>

<script>
import moment from "moment";
import Constants from "./constants";

export default {
  data () {
    return {
      selectedRestName : Constants.REST_NAME_MAP[this.$route.params.code]['name']
    }
  },
  name: 'App',
  watch:{
    $route () {
      this.selectedRestName = Constants.REST_NAME_MAP[this.$route.params.code]['name'];
    },
  },
  methods : {
    today: function () {
      if (this.$route.params.date == null) {
        return moment().format('YYYYMMDD');
      } else {
        return this.$route.params.date;
      }
    },
    getMealType: function() {
      if (this.$route.params.mealType == null) {
        return '2';
      } else {
        return this.$route.params.mealType;
      }
    }
  }
}

</script>

<style scoped>
  .router-link {
    font-weight: bold;
    color: black;
  }
</style>



